<template>
  <div class="a-scormViewer">
    <div id="navigationContainer" style="display: none;" />
    <div id="treeContainer" style="display: none;" />
    <div class="a-scormViewer__container placeholder_contentIFrame">
      <iframe
        id="contentIFrame"
        class="a-scormViewer__iframe"
        frameborder="0"
        @load="onScormLoad"
      />
    </div>
  </div>
</template>

<script>
import Vue from "vue";

export default Vue.extend({
  name: "FuScorm",
  data() {
    return {
      loaded: false,
    };
  },
  async created() {
    const libraries = [
      "sscompat.js",
      "sscorlib.js",
      "ssfx.Core.js",
      "API_BASE.js",
      "API.js",
      "API_1484_11.js",
      "Controls.js",
      "LocalStorage.js",
      "Player.js",
    ];
    for (const library of libraries) {
      await Vue.loadScript(`/scorms-component/js/scormpool/${library}`);
    }

    const manifestUrl = window.xprops?.manifestUrl;
    const withCache = window.xprops?.withCache;

    window.PlayerConfiguration.StorageSupport = true;
    await Promise.resolve(window.Run.ManifestByURL(manifestUrl, withCache));

    // ponerlo en un 'when iframe is loaded'
    const LMSGetValueMethod = (element) => window.API.LMSGetValue(element);
    const LMSSetValueMethod = (element, value) =>
      window.API.LMSSetValue(element, value);

    window.xprops.getApi(LMSGetValueMethod);
    window.xprops.setApi(LMSSetValueMethod);

    console.log("Dec 14 09:30-- SCORM");
  },
  methods: {
    onScormLoad() {
      console.log("scorm loaded zoid", this.loaded);
      if (this.loaded) {
        console.log("YA CARGO");
        window.xprops?.onLoad();
      }
      this.loaded = !this.loaded;
    },
  },
});
</script>
<style>
.a-scormViewer,
.a-scormViewer__container {
  height: 100%;
}
</style>
